import { classMap } from 'lit/directives/class-map.js';
import { html } from 'lit';
import { property, query } from 'lit/decorators.js';
import Avatar from '../avatar/avatar.component';
import Button from '../button/button.component';
import componentStyles from '../../styles/component.styles';
import Icon from '../icon/icon.component';
import styles from './list-item.styles';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';

/**
 * @summary list items provide options for the user to select in a list.
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net/?path=/docs/components-list-item--docs
 *
 * @dependency koerber-icon
 * @dependency koerber-button
 * @dependency koerber-avatar
 *
 * @slot label - list item's label text.
 * @slot prefix - list item's prefix icon or avatar.
 * @slot secondary-text - secondary text to the list item.
 * @slot tertiary-text - tertiary text to the list item.
 * @slot suffix - list item's suffix icon-only button.
 *
 * @csspart base - The component's base wrapper.
 */
export default class ListItem extends UIKitElement {
  static styles: CSSResultGroup = [componentStyles, styles];
  static dependencies = { 'koerber-avatar': Avatar, 'koerber-button': Button, 'koerber-icon': Icon };

  @query('slot:not([name])') defaultSlot: HTMLSlotElement;

  /** The label of the list item */
  @property() label = '';

  /** The secondary text of the list item */
  @property({ type: String, attribute: 'secondary-text' }) secondaryText = '';

  /** The tertiary text of the list item */
  @property({ type: String, attribute: 'tertiary-text' }) tertiaryText = '';

  /** The size of the list item */
  @property({ reflect: true }) size: 'small' | 'medium' | 'large' = 'medium';

  /** A unique value to store in the list item. This can be used as a way to identify list items when clicked. */
  @property() value: string;

  connectedCallback() {
    super.connectedCallback();
  }

  private getPrefixIcon() {
    return this.querySelector<Icon>('koerber-icon');
  }

  private getPrefixAvatar() {
    return this.querySelector<Avatar>('koerber-avatar');
  }

  private async syncPrefix() {
    const icon = this.getPrefixIcon();
    const avatar = this.getPrefixAvatar();
    const hasSubtext = this.secondaryText || this.tertiaryText;

    // Sync the size for prefix icon or avatar
    if (icon) {
      await icon.updateComplete;
      icon.size = hasSubtext || this.size === 'large' ? 'large' : 'medium';
    } else if (avatar) {
      await avatar.updateComplete;
      avatar.size = 'small';
    }
  }

  private getSuffixButton() {
    return this.querySelector<Button>('koerber-button');
  }

  private async syncSuffixButton() {
    const button = this.getSuffixButton();

    // Sync the size, variant, and icon-only for suffix button
    if (button) {
      await button.updateComplete;
      button.variant = 'ghost';

      // add 'text-container-has-suffix' class  if the suffix button exists.
      this.shadowRoot!.querySelector('.text-container')!.classList.add('text-container-has-suffix');
    }
  }

  render() {
    const hasSubtext = this.secondaryText || this.tertiaryText;

    return html`
      <div
        part="base"
        class=${classMap({
          'list-item': true,
          'line-item--has-subtext': hasSubtext,
          'line-item--has-no-subtext': !hasSubtext,
          'list-item--small': this.size === 'small',
          'list-item--medium': this.size === 'medium',
          'list-item--large': this.size === 'large'
        })}
      >
        <slot name="prefix" class="list-item__prefix" @slotchange=${this.syncPrefix}></slot>

        <div
          class=${classMap({
            'text-container': true,
            'text-container--has-subtext': hasSubtext
          })}
        >
          <slot name="label" class="list-item__label-text">${this.label}</slot>
          ${this.secondaryText
            ? html`<slot name="secondary-text" class="list-item__secondary-text">${this.secondaryText}</slot>`
            : ''}
          ${this.tertiaryText
            ? html`<slot name="tertiary-text" class="list-item__tertiary-text">${this.tertiaryText}</slot>`
            : ''}
        </div>

        <slot name="suffix" class="list-item__suffix" @slotchange=${this.syncSuffixButton}></slot>
      </div>
    `;
  }
}
